import React from 'react';
import styled from 'styled-components';
import { baseURL } from 'api';

interface IportfolioitemProps {
  id?: number;
  name?: string;
  detail?: string;
  lnk?: string;
  companyEn?: string;
}

const PortfolioItem: React.FunctionComponent<IportfolioitemProps> = ({
  id,
  name,
  detail,
  lnk,
  companyEn,
}) => {
  return (
    <Div>
      <a href={lnk} target="_blank" rel="noreferrer">
        <p>
          <img
            src={`${baseURL}img/career/${companyEn}/thumb${id}.jpg`}
            alt=""
          />
        </p>
        <dl>
          <dt>{name}</dt>
          <dd>{detail}</dd>
        </dl>
      </a>
    </Div>
  );
};

const Div = styled.div`
  background-color: ${({ theme }) => theme.colors.grayF};
  border-radius: 2rem;
  & + div {
    margin: 2rem 0;
  }
  p {
    overflow: hidden;
    width: inherit;
    height: 30rem;
    /* background-color: #f3d46b; */
    border-radius: 2rem 2rem 0 0;
    /* text-indent: -5000px; */
    img {
      width: 100%;
    }
  }
  dl {
    padding: 2rem;
    dt {
      padding-bottom: 0.5rem;
      font-weight: ${({ theme }) => theme.fonts.weight};
      font-size: ${({ theme }) => theme.fonts.size.base};
      color: ${({ theme }) => theme.colors.gray3};
    }
    dd {
      padding-bottom: 1.5rem;
      font-weight: ${({ theme }) => theme.fonts.weight};
      font-size: ${({ theme }) => theme.fonts.size.sm};
      color: ${({ theme }) => theme.colors.gray6};
    }
  }
`;

export default PortfolioItem;
